
.main-container{
   
}

.sub-title{
    @import url('https://fonts.googleapis.com/css?family=Lato:300&display=swap');
    font-family: 'Lato', sans-serif;
    position: relative;
    // top: 235px;
    left: 50%;
    width: 396px;
    height: 29px;
    text-align: left;
    letter-spacing: 0;
    opacity: 1;
    color: #FEFEFE;
    padding: 0;
    margin: 0;
    font-size: 1.5rem;
    margin-left: -200px;
    transition: all 1s;
    overflow: hidden;
    box-sizing: border-box;
    animation: initAnimatSub 1.2s;
    span{
        color: #FFDE00;

    }
    }

    .title{
        position: relative;
    box-sizing: border-box;
    // top: 238px;
    left: 50%;
    // width: 262px;
    text-align: left;
    letter-spacing: 0;
    opacity: 1;
    color: #FCFCFC;
    font-size: 4rem;
    margin: 0;
    padding: 19px 0px ;
    z-index: 2;
    padding: 0;
    margin-top: -5px;
    margin-left: -253px;
    transition: all 1s  ;
    overflow: hidden;
    animation: initAnimat .6s ease-in-out;
    
    &::first-letter{
        color: #FEDD00;
    }
    
}
h1:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    color: #FEDD00;
    overflow: hidden;
    width: 0%;
    transition: width 1s ease-out, color 1s ease-in .5s;
    


    
}


.active{
    animation: activeAnimat 1.2s;

    &::before{
       animation: beforAnimat 1.2s;
        
    }
}
.activeSub{
    animation: activeAnimatSub 1.2s;

    &::before{
       animation: beforAnimatSub 1.2s;
        
    }
}

.active1{
    &::before{
        content: "HOME";
    }
}
.active2{
    &::before{
        content: "ABOUT";
    }
}
.active3{
    &::before{
        content: "PORTFOLIO";
    }
}
.active4{
    &::before{
        content: "SKILLS";
    }
}
.active5{
    &::before{
        content: "CONTACT";
    }
}



@keyframes beforAnimat{
    0% {width: 0%;}
    50% {width: 200px;}
    100% {width: 0%;}
}

@keyframes activeAnimat{
    0% {width: 200px;}
    50% {width: 200px;}
    100% {width: 0%;}
}

@keyframes beforAnimatSub{
    0% {width: 0%;}
    50% {width: 400px;}
    100% {width: 0%;}
}

@keyframes activeAnimatSub{
    0% {width: 400px;}
    50% {width: 400px;}
    100% {width: 0%;}
}



// @keyframes initAnimatSub{
//     from {width: 0%;}
//     to {width: 500px;}
// }
@keyframes initAnimat{
    from {width: 0%;}
    to {width: 200px;}
}


@media(max-width:580px){
    .sub-title{
        font-size: 1rem;
        width: 261px;
        margin-left: -100px;

    }

    .title{
        font-size: 3rem;
        width: 280px;
        margin-left: -153px;

    }
}