
.about-container{
height: 335px;
}
 
.content-about{
display: flex;
margin-left: 51px;
}
.img-contant{
    display: flex;
    flex-direction: column;
    position: relative;
    margin-left: 25px;
    margin-top: -33px;
}
    .paragraph{
        display: -webkit-box;
        margin: 0;
        height: 234px;
        text-align: left;
        font: Light 16px/22px Lato;
        letter-spacing: 0;
        color: #FCFCFC;
        line-break: 10px;
        font-weight: 300;
        // margin-top: 38px;
        margin-left: 110px;
        line-height: 21px;
        transform-origin: top left;
        transform: translateX(-500%);
        opacity: 0;
        transition: all 1.2s ease-out;
        &::before{
            display: block;
            width: 16px;
            height: 204px;
            content: '';
            margin-right: 13px;
            background-color: #FEDD00;
        }
    }

    .img-container-4{
        width: 198px;
        height: 52px;
        background-image: url('../../assets/imags/aboutImg/about4.png');
        margin-top: 12px;
        margin-left: 55px;
        transform: translateY(500%);
        opacity: 0;
        transition: all .3s ease-out .8s;
    }
    .img-container-3{
        width: 194px;
        height: 52px;
        background-image: url('../../assets/imags/aboutImg/about3.png');
        margin-top: 12px;
        margin-left: 78px;
        opacity: 0;
        transform: translateY(500%);
        transition: all .3s ease-out .6s;
    }
    .img-container-2{
        width: 188px;
        height: 52px;
        background-image: url('../../assets/imags/aboutImg/about2.png');
        margin-top: 12px;
        margin-left: 43px;
        transform: translateY(500%);
        opacity: 0;
        transition: all .3s ease-out .4s;
    }
    .img-container-1{
        width: 329px;
        height: 52px;
        background-image: url('../../assets/imags/aboutImg/about1.png');
        transform: translateY(500%);
        opacity: 0;
        transition: all .3s ease-out .2s;
    }



    .init{
        transform: rotateY(360deg);
        transform: translateY(0%);
        opacity: 1;
    }


    @media(max-width:580px){

        .about-container{
            margin-top: 220px;
        }
        .img-contant{
            margin-top: 0px;
            margin-left: 0px;
            width: 210px;
        }
        .img-container-4,.img-container-3,.img-container-2,.img-container-1{
            transform: scale(.7);
            margin-top: -5px;
        
        }
        .img-container-1{
            width: 230px;
        }
        .img-container-2{
            width: 180px;
        }
        .img-container-3{
            width: 142px;
        }
        .img-container-4{
            width: 166px;
        }


        .paragraph{
            font-size: .7rem;
            line-height: normal;
            width: 128px;
            margin-left: 0px;
            &::before{
                height: 250px;
            }

        }
    }